import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { withRouter } from "react-router-dom";
import { createUser, getUser } from "../../actions";
import { PASSWORD_MIN_LENGTH, USERNAME_MAX_LENGTH } from "../../constants";

class SignUpForm extends Component {
  renderField = ({ input, label, type, meta: { touched, error } }) => {
    const className = `field ${error && touched ? "error" : ""}`;
    return (
      <div className={className}>
        <label>{label}</label>
        <input {...input} placeholder={label} type={type} />
        {touched && error && (
          <p style={{ color: "maroon", marginTop: "5px" }}>{error}</p>
        )}
      </div>
    );
  };

  onSignUpFormSubmit = async values => {
    const { createUser, history, getUser } = this.props;
    const token = await createUser(values).catch(() => {});
    if (token) {
      localStorage.setItem("baiziUserToken", token);
      history.push("/read");
      getUser();
    }
  };

  render() {
    const { handleSubmit, submitting, invalid, errorMessage } = this.props;

    return (
      <div
        className="ui segment"
        style={{ width: "90%", maxWidth: "500px", margin: "0 auto" }}
      >
        <h1 className="ui center aligned header">æ³¨å†Œ</h1>
        <form
          className="ui form"
          onSubmit={handleSubmit(this.onSignUpFormSubmit)}
        >
          <Field
            name="username"
            label="ç”¨æˆ·å"
            component={this.renderField}
            type="text"
          />
          <Field
            name="password"
            label="å¯†ç "
            component={this.renderField}
            type="password"
          />
          <button
            className="ui button"
            type="submit"
            disabled={submitting | invalid}
          >
            æ³¨å†Œ
          </button>
        </form>
        {errorMessage && (
          <p style={{ color: "red", marginTop: "5px" }}>{errorMessage}</p>
        )}
      </div>
    );
  }
}

const validate = values => {
  const errors = {};
  const regex = new RegExp("^[-\\w@\\.]{1," + USERNAME_MAX_LENGTH + "}$");
  if (!values.username) {
    errors.username = "ç”¨æˆ·åä¸èƒ½ä¸ºç©º";
  } else if (!values.username.match(regex)) {
    errors.username = `ç”¨æˆ·ååªèƒ½åŒ…å«è‹±è¯­ã€æ•°å­—ã€â€œ_â€ã€â€œ.â€å’Œâ€œ@â€ï¼Œä¸”ä¸èƒ½è¶…è¿‡${USERNAME_MAX_LENGTH}ä¸ªå­—ç¬¦`;
  }
  if (!values.password || values.password.length < 6) {
    errors.password = `å¯†ç è‡³å°‘éœ€è¦${PASSWORD_MIN_LENGTH}ä½æ•°`;
  }
  return errors;
};

const mapStateToProps = state => ({
  errorMessage: state.user.errorMessage
});

export default withRouter(
  connect(
    mapStateToProps,
    { createUser, getUser }
  )(
    reduxForm({
      form: "SignUp",
      validate
    })(SignUpForm)
  )
);
