export const getUser = () => async dispatch => {
  const userInfo = await userApi.getUser().catch(err => console.log(err));
  console.log(userInfo);
  if (userInfo) {
    dispatch({
      type: types.GET_USER,
      payload: userInfo
    });
  }
};

export const logoutUser = () => {
  return {
    type: types.LOGOUT_USER
  };
};
